body {
    background-color: whitesmoke;
    text-align: center;
    font-family: sans-serif;
}

h2 {
    font-size: 40px;
}

input {
    width: 300px;
    margin: 5px;
}

.deleteButton {
    color: aliceblue;
    background-color: brown;
    position: absolute;
    right: 70px
}

li {
    margin: 4px;
    float: center;
}

.notification {
    color: green;
    padding: 5px;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: rgb(215, 233, 215);
    border: solid 3px rgb(0, 70, 0);
    border-radius: 5px
}

.error {
    color: rgb(128, 0, 0);
    padding: 5px;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: rgb(233, 215, 215);
    border: solid 3px rgb(70, 0, 0);
    border-radius: 5px
}


.app {
    position: absolute;
    left: 35%;
    text-align: center;
    border: solid black 3px;
    border-radius: 10px;
    width: 600px;
    background-color: wheat;
    padding: 5px
}